<template>
  <OmniChat
    v-if="omniChatStore.controller && omniChatStore.repository"
    :ai-chat-repository="omniChatStore.repository"
    :controller="omniChatStore.controller"
    :environment="{
      origin: 'console',
      projectId: projectId,
    }"
  />
</template>

<script lang="ts" setup>
import OmniChat from '@/services/ai/OmniChat.vue';
import { useOmniChatStore } from '@/services/ai/omniChatStore';
import { useRoute } from 'vue-router';

const route = useRoute();
const projectId = route.params.projectId as string;

const omniChatStore = useOmniChatStore();
omniChatStore.init('console', projectId);
</script>
